
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

export default {
components: {
InlineXNumber
},
data() {
return {
cartInfo: [
{
id: 0,
img:
'http://img.hb.aicdn.com/1f328be1adcc60404728f890f6c2b80ea506eb9842737-Zf8bpf_fw658',
name:
'vv  ',
price: '100.00',
count: 1
},
{
id: 1,
img:
'http://img.hb.aicdn.com/1f328be1adcc60404728f890f6c2b80ea506eb9842737-Zf8bpf_fw658',
name:
'vv  ',
price: '100.00',
count: 1
}
],
idArr: [],
totalPrice: '9000.00'
}
},
computed: {
status() {
if (this.cartInfo.length > 0) {
return true
} else {
return false
}
}
},
created() {},
mounted() {},
methods: {

jSelect(id) {
let f = false
this.idArr.filter(o => {
if (o == id) f = true
})
return f
},

selectSku(sid, event) {
let f = event.target.checked
if (event.target.checked == true) {
this.idArr.push(sid)
} else {
this.idArr = this.idArr.filter(o => {
return o != sid
})
}
console.log(JSON.stringify(this.idArr))
},

chooseAll (event) {
this.idArr = []
if(event.target.checked){
this.cartInfo.filter(item=>{
this.idArr.push(item.id)
})
}
}
}
}



