export default {
namespaced:true,
   state:{
    navBar:true,    //  
    showBack:true,  //  
    showMore:true,  //  
    navTitle:'',    //  
    tabBar:true,    //  
    tabIndex:0,     //  
   },
   mutations:{
       setnavBar(state,data){
        state.navBar = data
       },
       setshowBack(state,data){
        state.showBack = data
       },
       setshowMore(state,data){
        state.showMore = data
       },
       setnavTitle(state,data){
        state.navTitle = data
       },
       setTab(state,data){
           state.tabBar = data
       },
       setTabIndex(state,data){
           state.tabIndex = data
       }
   }
}


